import React from 'react';

/**
 * component to render loading icon 
 * @author Tom Hegarty
 * 
 * @return <div className="loader">
 */
class Loader extends React.Component {
    render() {
      if(this.props.loader){
        return (<div className="loader"></div>)
      }else{
        return ""
      }
    }
}

export default Loader;