import React from 'react';
/**
 * A Component to render the author award filter
 * @author Tom Hegarty
 *  
 * @props award {String} all possible awards returned from fetch 
 * @props handleAwardSelect {Function} to filter data beased on selection
 * 
 * @return  <div className="awardSearch">
 */
class SelectAward extends React.Component {

    render (){

      return (
        <div className="awardSearch">
          <label>
         Filter by Award : 
         <select id="AuthorAwardInput" className="dropMenu" value={this.props.award} onChange={this.props.handleAwardSelect}>
           <option value="">Any</option>
           <option value="HONORABLE_MENTION">Honorable Mentions</option>
           <option value="BEST_PAPER">Best Papers</option>
         </select>
       </label>
     </div>
      )
    }
}
export default SelectAward; 