import React from 'react';
import Presentations from './Presentations.js';

/**
 * component to show sessions within a slot on the schedule 
 * @author Tom Hegarty
 * 
 * @props details {Obj} details os session reurend from fetch in Slot.js
 * @returns <div className="SessionsBox">
 */
class Sessions extends React.Component {

    constructor(props) {
      super(props);
      this.state = {
        data: [],
        display: false,
        activeWidth: ""
      }
    }
  
  
    handlePresentationClick = () => { 
        this.setState({display:!this.state.display})
        this.handleWidth();
        this.loadPresentationDetails();
    } 

    handleWidth = () => {

    }
  
    loadPresentationDetails = () => {
      const url = ("http://unn-w16017590.newnumyspace.co.uk/kf6012/part1/api/sessionpresentations?session_id=" + this.props.details.sessionId);
  
      fetch(url)
        .then( (response) => response.json() )
        .then( (data) => {
          this.setState({
            data:data.data,
            loading: false
          })
        })
      .catch ((err) => {
        console.log("something went wrong ", err)
        }  
      );

      if(this.state.display === false){
        this.setState({activeWidth:"90%"})
      }else{
        this.setState({activeWidth:""})
      }
    }
  
    render () {
  
      let presentations = "";
      let buttonValue = "Show Presentations ▼";


  
      if (this.state.display) {
          presentations = this.state.data.map( (details,i) => (<Presentations key={i} details={details}/>))
          buttonValue =  "Hide Presentations ▲";
        }else{
          buttonValue =  "Show Presentations ▼";
        }
  
  
      return (
        <div className="SessionsBox" style={{width : this.state.activeWidth}}>
          <b><span className="underline">{this.props.details.name}</span></b>
          <p><b>Type of Session: </b>{this.props.details.Type}<br></br>
          <b>Room:</b> {this.props.details.Room} <br></br>
          <b>Session Chair:</b> {this.props.details.Chair}</p>
          <button onClick={this.handlePresentationClick} >{buttonValue}</button>
          {presentations}
        </div>
      )
    }
  }

  export default Sessions; 