import React from 'react';
import Login from './Login.js';
import Update from './Update.js';


class Admin extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      "authenticated":false,
      "email":"", 
      "password":"", 
      "errormsg" : "",
      "admin" : "",
      "SignUpName" : "",
      "SignUpEmail" : "",
      "SignUpPassword" : ""
    }
 
    this.handleEmail = this.handleEmail.bind(this);
    this.handlePassword = this.handlePassword.bind(this);
  }

  loginCallback = (data) => {
    if (data.status === "200") {
      this.setState({"authenticated":true, "admin": data.admin, "errormsg" : "Log in Successful, Welcome", "admin": data.admin});
      localStorage.setItem('myToken', data.token);
      localStorage.setItem('Admin', data.admin);
    } else {
      this.setState({"errormsg" : "Incorrect Credentials, Try Again"});
    }
  }
  
  updateCallback = (data, myJSON) => {
    if (data.status == "200"){
      this.setState({"errormsg": "record updated!"})
      alert("Title Updated to '" + myJSON.title + "'");
      window.location.reload(); 
    }
    else if(data.status == "401"){
      this.setState({"errormsg": "You are not authorised to update titles"})
      this.setState({"authenticated":false, "admin": ""})
      localStorage.removeItem('myToken');  
      localStorage.removeItem('Admin');
    } else {
      this.setState({"authenticated":false, "admin": ""})
      localStorage.removeItem('myToken');  
      localStorage.removeItem('Admin');
      this.setState({"errormsg": "Token expired, you have been logged out"})
    }
  }

  signUpCallback = (data) => {
    if (data.status == "200")
    this.setState({"errormsg": "new user created! try loggin in"})
    else if (data.status == "500"){
      this.setState({"errormsg": "Account alreay exists, try a diferent name and email"})
    } else {
      this.setState({"errormsg": "Error singing up, try different credentials"})
    }
  }

  postData = (url, myJSON, callback) => {
    fetch(url, {   method: 'POST',
                   headers : new Headers(),
                   body:JSON.stringify(myJSON)})
      .then( (response) => response.json() )
      .then( (data) => {
        callback(data, myJSON)
      })
      .catch ((err) => {
        console.log("something went wrong ", err)
        console.log(JSON.stringify(myJSON));
        this.setState({"errormsg": "Error fetching data"})
      }
    );
  }

  handleLoginClick = () => {
    const url = "http://unn-w16017590.newnumyspace.co.uk/kf6012/part1/api/login"
    let myJSON = {"email":this.state.email, "password":this.state.password}
    if(this.state.email === "" || this.state.password === ""){
      this.setState({"email":null, "password":null})
    }
    this.postData(url, myJSON, this.loginCallback)
  }

  handleLogoutClick = () => {
    localStorage.removeItem('myToken'); 
    localStorage.removeItem('Admin');
    this.setState({"authenticated":false, "admin":"", "errormsg": "You have logged out"})
    this.setState({"email":null, "password":null})
  }
  
  handleUpdateClick = (sessionId, title) => {
    const url = "http://unn-w16017590.newnumyspace.co.uk/kf6012/part1/api/update"
  
    if (localStorage.getItem('myToken')) {
      let myToken = localStorage.getItem('myToken')
      let myJSON = {
          "token": myToken,
          "sessionId": sessionId,
          "title": title
        }
        this.postData(url, myJSON, this.updateCallback)
      } else {
        this.setState({"errormsg": "Error authenticating, record not updated"})
      }
   } 

  handleSignUpClick = (name, email, password) => {
    const url = "http://unn-w16017590.newnumyspace.co.uk/kf6012/part1/api/adduser"

      let myJSON = {
        "username": this.state.SignUpName,
        "email": this.state.SignUpEmail,
        "password": this.state.SignUpPassword
      }
      this.postData(url, myJSON, this.signUpCallback)

  }

  componentDidMount() {
    if(localStorage.getItem('myToken')) {
      this.setState({"authenticated":true});
    } 
    if(localStorage.getItem('Admin')) {
      this.setState({"admin":localStorage.getItem('Admin')});
    }
  }

  handlePassword = (e) => {
    this.setState({password:e.target.value})
  }
  handleEmail = (e) => {
    this.setState({email:e.target.value})
  }

  handleSignUpName = (e) => {
    this.setState({SignUpName:e.target.value})
  }
  handleSignUpEmail = (e) => {
    this.setState({SignUpEmail:e.target.value})
  }
  handleSignUpPassword = (e) => {
    this.setState({SignUpPassword:e.target.value})
  }

  render() {
    let page = <Login 
      handleLoginClick={this.handleLoginClick}
      email={this.state.email}
      password={this.props.password}
      handleSignUpClick={this.handleSignUpClick}
      SignUpName={this.state.SignUpName}
      SignUpEmail={this.state.SignUpEmail}
      SignUpPassword={this.state.SignUpPassword}
      handleEmail={this.handleEmail}
      handlePassword={this.handlePassword}
      handleSignUpName={this.handleSignUpName}
      handleSignUpEmail={this.handleSignUpEmail}
      handleSignUpPassword={this.handleSignUpPassword}
    />
    if (this.state.authenticated){
        page = <div>
                <button className="LogOutButton" onClick={this.handleLogoutClick}>Log out</button>
                <Update handleUpdateClick={this.handleUpdateClick} admin={this.state.admin}/>             
              </div>
       } 
  
    return (
      <div>
        <h1>Admin</h1>
        <p>{this.state.errormsg}</p>
        {page}
      </div>
    );
  }
}

export default Admin;