import React from 'react';
import Author from './Author.js';
import AuthorSearch from './AuthorSearch.js';
import SelectAward from './SelectAward.js';
import Loader from './Loader.js';
import PageButtons from './PageButtons.js';
import Footer from './Footer.js';

/**
 * Parent component for Authors page
 * @author Tom Hegarty 
 * 
 * 
 * @returns <div className = "Authors">
 */

class Authors extends React.Component {

    constructor(props) {
      super(props);
      this.state = {
        page:1,
        pageSize:10,
        query:"",
        award: "",
        loading: true,
        data : []
      }
      this.handleSearch = this.handleSearch.bind(this);
      this.handleAwardSelect = this.handleAwardSelect.bind(this);
    }
  
    handleMoreClick = () => {
      this.setState({page:this.state.page+1})
    }
    
    handleSearch = () => {
      let SearchValue = (document.getElementById("AuthorSearchInput").value).toLowerCase();
      this.setState({query:SearchValue, page:1})
      const url = ("http://unn-w16017590.newnumyspace.co.uk/kf6012/part1/api/authorPres?search=" + (SearchValue) + "&award=" + this.state.award +  "&page=" + (this.state.page));

      fetch(url)
        .then( (response) => response.json() )
        .then( (data) => {
          this.setState({data:data.data})
          this.setState({loading:false})
        })
      .catch ((err) => {
        console.log("something went wrong ", err)
        }  
      );
    }
  
    handleAwardSelect = () => {
      let AwardValue = (document.getElementById("AuthorAwardInput").value);
      this.setState({award:AwardValue, page:1})
      const url = ("http://unn-w16017590.newnumyspace.co.uk/kf6012/part1/api/authorPres?search=" + (this.state.query) + "&award=" + AwardValue + "&page=" + (this.state.page));

      fetch(url)
        .then( (response) => response.json() )
        .then( (data) => {
          this.setState({data:data.data})
          this.setState({loading:false})
        })
      .catch ((err) => {
        console.log("something went wrong ", err)
        }  
      );
    }
  
    searchDetails = (details) => {
      return details.name.toLowerCase().includes(this.state.query)
    }
  
    selectAwardDetails = (details) => {
      return ((this.state.award === details.award) || (this.state.award === ""))
    }
  
    handlePreviousClick = () => {
      this.setState({page:this.state.page-1})
      window.scrollTo(0, 0)

      const url = ("http://unn-w16017590.newnumyspace.co.uk/kf6012/part1/api/authorPres?search=" + (this.state.query) + "&award=" + this.state.award + "&page=" + (this.state.page - 1));
  
      fetch(url)
        .then( (response) => response.json() )
        .then( (data) => {
          this.setState({data:data.data})
          this.setState({loading:false})
        })
      .catch ((err) => {
        console.log("something went wrong ", err)
        }  
      );

    }
    
    handleNextClick = () => {
      this.setState({page:this.state.page+1})
      window.scrollTo(0, 0)

      const url = ("http://unn-w16017590.newnumyspace.co.uk/kf6012/part1/api/authorPres?search=" + (this.state.query) + "&award=" + this.state.award + "&page=" + (this.state.page + 1));
  
      fetch(url)
        .then( (response) => response.json() )
        .then( (data) => {
          this.setState({data:data.data})
          this.setState({loading:false})
        })
      .catch ((err) => {
        console.log("something went wrong ", err)
        }  
      );
    }
  
  
    componentDidMount() {
      const url = ("http://unn-w16017590.newnumyspace.co.uk/kf6012/part1/api/authorPres?page=1");
  
      fetch(url)
        .then( (response) => response.json() )
        .then( (data) => {
          this.setState({data:data.data})
          this.setState({loading:false})
        })
      .catch ((err) => {
        console.log("something went wrong ", err)
        }  
      );
    }
    
    render() {

      let disabledPrevious = (this.state.page <= 1)
      let disabledNext = (this.state.data.length < 10)

      return (
        <div className = "Authors">
          <h1>Authors</h1>
          <div className ="searchMenu">
          <AuthorSearch handleSearch={this.handleSearch}/>
            {<SelectAward value={this.state.award} handleAwardSelect={this.handleAwardSelect} /> }
          </div>
          <Loader loader={this.state.loading}/>
          <div className="authorCont">
          {this.state.data
           .map( (details, i) => (<Author key={i} details={details} />) )}
           </div>
          <PageButtons page={this.state.page} disabledPrevious={disabledPrevious} disabledNext={disabledNext} handleNextClick={this.handleNextClick} handlePreviousClick={this.handlePreviousClick}/>
          <Footer />  
        </div>
      );    
    }
  }

export default Authors; 