import React from 'react';
import UpdateItem from './UpdateItem.js';
import Search from '../Search.js';
import Loader from '../Loader.js';
import PageButtons from '../PageButtons.js';

class Update extends React.Component {

state = {
    data:[],
    query: "",
    loading: true,
    page:1,
    pageSize:10
}


componentDidMount() {
    const url = "http://unn-w16017590.newnumyspace.co.uk/kf6012/part1/api/sessions"
    fetch(url)
    .then( (response) => response.json() )
    .then( (data) => {
        this.setState({data:data.data})
        this.setState({loading:false})
    })
    .catch ((err) => {
        console.log("something went wrong ", err)
    }
    );
}

searchTitle = (details) => {
  return details.SessionName.toLowerCase().includes(this.state.query)
}

handleSearch = (e) => {
  this.setState({query:e.target.value})
}

handlePreviousClick = () => {
  this.setState({page:this.state.page-1})
  window.scrollTo(0, 0)
}

handleNextClick = () => {
  this.setState({page:this.state.page+1})
  window.scrollTo(0, 0)
}

render() {
    let filteredData =  (
      this.state.data       
      .filter(this.searchTitle)
    )

    let noOfPages = Math.ceil(filteredData.length/this.state.pageSize)
    if (noOfPages === 0) {noOfPages=1}

    let disabledPrevious = (this.state.page <= 1)
    let disabledNext = (this.state.page >= noOfPages)

    return (
      
      <div className="updateSessions">
        <div className="adminSearch">
          <Search query={this.state.query} handleSearch={this.handleSearch}/>
        </div>
        <Loader loader={this.state.loading}/>
        {filteredData
           .slice(((this.state.pageSize*this.state.page)-this.state.pageSize),(this.state.pageSize*this.state.page))
           .map( (details, i) => (<UpdateItem key={i} details={details} admin={this.props.admin} handleUpdateClick={this.props.handleUpdateClick}/>) )}
        <PageButtons page={this.state.page} noOfPages={(" of " + noOfPages)} disabledNext={disabledNext} disabledPrevious={disabledPrevious} handleNextClick={this.handleNextClick} handlePreviousClick={this.handlePreviousClick}/>
      </div>
    );
  }
}

export default Update;