import React from 'react';
import Slot from './Slot.js';
/**
 * component to return list of slots within a day 
 * @author Tom Hegarty 
 * 
 * @props dayInt {int} intergery value of day 
 * @returns <div className="Day">
 */
class Day extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
          data : [],
          display: false,
          slots: "",
          loading: true
        }
    }
  
    loadSlotDetails = () => {
      const url = ("http://unn-w16017590.newnumyspace.co.uk/kf6012/part1/api/slots?day=" + this.props.dayInt);
  
      fetch(url)
        .then( (response) => response.json() )
        .then( (data) => {
          this.setState({
            data:data.data,
            loading: false
          })
        })
      .catch ((err) => {
        console.log("something went wrong ", err)
        }  
      );
    }
  
    handleSlotClick = () => {
      this.setState({display:!this.state.display})
      this.loadSlotDetails();
    } 
  
    render() {
      let slots = "";
      let buttonValue = "Show Slots ▼";
  
      if (this.state.display) {
          slots = this.state.data.map( (details,i) => (<Slot key={i} details={details} slotCount={i+1}/>))
          buttonValue =  "Hide Slots ▲";
        }else{
          buttonValue =  "Show Slots ▼";
        }
  
      return (
        <div className="Day">
          <h2>{this.props.day}</h2>
          <button id="detailsButton" onClick={this.handleSlotClick}>{buttonValue}</button>
          <div className="slotCont">
            {slots}
          </div>
        </div>
      )
    }
  }

  export default Day; 