import React from 'react';
import { NavLink } from "react-router-dom";
import Collapsible from 'react-collapsible';
import Hamburger from 'hamburger-react';

/**
 * Component to return navigation menu
 * @author Tom Hegarty 
 * 
 * @return <div  className="NavMenu">
 */
class NavMenu extends React.Component {

    constructor(props) {
      super(props);
      this.state = {
        isDesktop: false, 
        value: "Dark Mode",
        menuOpen: "☰"
      };

      this.updatePredicate = this.updatePredicate.bind(this);
    }

    componentDidMount() {
      this.updatePredicate();
      window.addEventListener("resize", this.updatePredicate);
    }
  
    componentWillUnmount() {
      window.removeEventListener("resize", this.updatePredicate);
    }
  
    updatePredicate() {
      this.setState({ isDesktop: window.innerWidth > 1031 });
    }
  
    handleLightDark = () => {
      if(this.state.value == "Dark Mode"){
        this.setState({value:"Light Mode"})
        document.documentElement.style.setProperty('--background-color', '#333333');
        document.documentElement.style.setProperty('--main-color-1', '#f3f3f3');
      }else{
        this.setState({value:"Dark Mode"})
        document.documentElement.style.setProperty('--background-color', 'white');
        document.documentElement.style.setProperty('--main-color-1', 'black');
      }
    }

  
    render () {
      const isDesktop = this.state.isDesktop;

      return (
        <div>
          {isDesktop ? (
            <div  className="NavMenu">
              <ul className="NavContain">
                <li>
                  <NavLink to="/" exact activeClassName="activeLink">Schedule</NavLink>
                </li>
                <li>
                  <NavLink to="/authors" exact activeClassName="activeLink">Authors</NavLink>
                </li>
                <li>
                  <NavLink to="/admin" exact activeClassName="activeLink">Admin</NavLink>
                </li>
                <button onClick={this.handleLightDark}>{this.state.value}</button>
              </ul>
            </div>
          ) : (
            <div  className="NavMenu">
            <Collapsible trigger={<Hamburger size={25} hideOutline={true}/>}>
              <ul className="NavContain">
                <li>
                  <NavLink to="/" exact activeClassName="activeLink">Schedule</NavLink>
                </li>
                <li>
                  <NavLink to="/authors" exact activeClassName="activeLink">Authors</NavLink>
                </li>
                <li>
                  <NavLink to="/admin" exact activeClassName="activeLink">Admin</NavLink>
                </li>
                <button onClick={this.handleLightDark}>{this.state.value}</button>
                </ul>
            </Collapsible>
          </div>
          )}
        </div>
      );
    }
}

export default NavMenu;