import React from 'react';

class Login extends React.Component {

  render(){
    return (
      <div className="AdminTop">
        <div className="LoginArea">
        <h3>Sign into Existing Account</h3>
        <input
          type='text' 
          placeholder='email'
          value={this.props.email}
          onChange={this.props.handleEmail}
        />
        <input
          type='password' 
          placeholder='password'
          value={this.props.password}
          onChange={this.props.handlePassword}
        />
        <button className="loginButton" disabled={!this.props.email} onClick={this.props.handleLoginClick}>Log in</button>
        </div>
        <div className="SignUpArea">
          <h3>Create new Account</h3>
        <input
          type='text' 
          placeholder='Enter Username'
          value={this.props.SignUpName}
          onChange={this.props.handleSignUpName} 
        />
        <input
          type='text' 
          placeholder='Enter Email'
          value={this.props.SignUpEmail}
          onChange={this.props.handleSignUpEmail}
        />
        <input
          type='text' 
          placeholder='Create Password'
          value={this.props.SignUpPassword}
          onChange={this.props.handleSignUpPassword}
        />
        <button className="SignUpButton" onClick={this.props.handleSignUpClick}>Sign Up</button>
        </div>
      </div>
    );
  }
}

export default Login;