import React from 'react';

/**
 * A Compontent to handle the page buttons for Author page
 * @author Tom Hegarty
 * 
 * @props page {int} the index of the page being displayed 
 * @props noOfPages {int} total number of avaible pages 
 * @props handlePreviousClick {function} to go back 1 page
 * @props handleNextClick {function} to go back 1 page 
 * @props disabledNext {int} disables form button if on first page
 * @props disabledPrevious {int} disables form button if on last page  
 * 
 * @return <div className="pageButtons">
 */

class PageButtons extends React.Component {
    render () {
      return(     
        <div className="pageButtons">
          <button onClick={this.props.handlePreviousClick} disabled={this.props.disabledPrevious}>◀ Previous</button>
          <p>Page {this.props.page} {this.props.noOfPages}</p>
          <button onClick={this.props.handleNextClick} disabled={this.props.disabledNext}>Next ▶</button>
        </div>
      )
    }
}

export default PageButtons;