import React from 'react';
import Sessions from './Sessions.js';


/**
 * component to return the contents of a Slot within a day
 * @author Tom Hegarty
 * 
 * @props details {Obj} data returend when fecthing slot details from Day.js
 * @returns  <div className="slot"> || <div className="slotBreak"
 */
class Slot extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        data: [],
        display: false
      }
    }
    
    loadSessionDetails = () => {
      //TODO _- this URL needs updated in the api to say sessions 
      const url = ("http://unn-w16017590.newnumyspace.co.uk/kf6012/part1/api/presentations?slotid=" + this.props.details.slotId);
  
      fetch(url)
        .then( (response) => response.json() )
        .then( (data) => {
          this.setState({
            data:data.data,
            loading: false
          })
        })
      .catch ((err) => {
        console.log("something went wrong ", err)
        }  
      );
    }
    
    handleSessionClick = () => {
      this.setState({display:!this.state.display})
      this.loadSessionDetails();
    } 
  
    render() {
  
      function lowerCaseAllWordsExceptFirstLetters(string) {
          return string.replace(/\S*/g, function (word) {
              return word.charAt(0) + word.slice(1).toLowerCase();
          });
      }
  
      function minuteCorrector(time) {
        if(time == 0){
          return ( time = ("0" + time).slice(-2))
        }else{
          return (time);
        }
      }
  
      let sessions = "";
      let buttonValue = "Show Sessions ▼";
  
      if (this.state.display) {
          sessions = this.state.data.map( (details,i) => (<Sessions key={i} details={details}/>))
          buttonValue =  "Hide Sessions ▲";
        }else{
          buttonValue =  "Show Sessions ▼";
        }
   
      if (this.props.details.Type === "SESSION"){
        return (
          <div className="slot">
            <h2><span className="underline">{this.props.details.Day} Slot {this.props.slotCount}</span></h2>
            <p>{this.props.details.startHour}:{minuteCorrector(this.props.details.startMinute)} - {this.props.details.endHour}:{minuteCorrector(this.props.details.endMinute)}</p>
            <button onClick={this.handleSessionClick}>{buttonValue}</button>
            <div className="sessionsCont">
              {sessions}
            </div>
          </div>
        );
      }else{
        return (
          <div className="slotBreak">
            <p><b>{lowerCaseAllWordsExceptFirstLetters(this.props.details.Type)} : </b>
            {this.props.details.startHour}:{minuteCorrector(this.props.details.startMinute)} - {this.props.details.endHour}:{minuteCorrector(this.props.details.endMinute)}</p>
          </div>
        );
      }
    
    }
  }

  export default Slot;