import React from 'react';


/**
 * A componetn to render detaisl of presentions within a slot 
 * @author Tom Hegarty 
 * 
 * @props details {obj} details of session  
 * @returns <div className="presentation">   
 */
class Presentations extends React.Component {

    constructor(props) {
      super(props);
      this.state = {
        data: [],
        display: false
      }
    }
  
    handleDetailsClick = () =>{
      this.setState({display:!this.state.display})
    }
  
      render() {
  
        function toTitleCase(str) {
          return str.replace(
            /\w\S*/g,
            function(txt) {
              return txt.charAt(0).toUpperCase() + txt.substr(1).toLowerCase();
            }
          );
        }
  
        let presentations = "";
        let award = "";
        let buttonValue = "Details ▼";
    
        if (this.state.display) {
            presentations =     <div>
            <p><b> Authors: </b> {this.props.details.authors}</p>
            <p><b> Abstract: </b> {this.props.details.abstract}</p>
          </div>;
            buttonValue =  "Hide ▲";
          }else{
            buttonValue =  "Details ▼";
        }
  
        if(this.props.details.award !== ""){
            award = ("🏆 " + toTitleCase(this.props.details.award).replace('_', ' '));
  
        }
  
        return(
          <div className="presentation">       
            <p><b>{this.props.details.title}</b></p>
            <p> {award}</p>
            <button onClick={this.handleDetailsClick} >{buttonValue}</button>
            {presentations}
          </div>
  
          );
      }
  }

  export default Presentations; 