import React from 'react';

/**
 * A Component to render the author search bar and award filter
 * @author Tom Hegarty 
 * 
 * @props query {String} search bar text input 
 * @props handleSearch {Function} to filter data by text input 
 * 
 * @return {JSX} <div className="searchBar">
 */
class AuthorSearch extends React.Component {
    render() {
      return (
        <div className="searchBar">
          <p className = "SearchText">Search: </p>
          <input id="AuthorSearchInput"
            className = "searchInput"
            type='text' 
            placeholder='search'
          />
          <button id="AuthorSearchButton" onClick={this.props.handleSearch}> Search </button>
        </div>
      )
    }       
}

export default AuthorSearch; 