import React from 'react';
/**
 * component to show futehr detaisl of an authors paper 
 * @author Tom Hegarty
 * 
 * @props Author.details  
 * @return <div className="paperDisplay">
 */
class AuthorPresentations extends React.Component {
    state = { 
        further: false
    }

    render() {
        let papersButtonValue = "Hide ▲";

        //database stores oclock values as "0", changes them to be "00" , 12:00 instead of 12:0
        if(this.props.details.startMinute == 0){
            this.props.details.startMinute = ("0" + this.props.details.startMinute).slice(-2); 
        }

        return (
            <div className="paperDisplay">
                <p>
                    <b>Title:</b> {this.props.details.title}<br></br>
                    <b>Start Time: </b>{this.props.details.startHour}:{this.props.details.startMinute}<br></br>
                    <b>Room: </b> {this.props.details.roomName}<br></br>
                    <b>Awards: </b>{(this.props.details.award).toLowerCase().replace('_', ' ')} 
                </p>
            </div>
        );
    }
}

export default AuthorPresentations;