import React from 'react';
import Loader from './Loader.js';
import Footer from './Footer.js';
import Day from './Day.js';

/**
 * Parent component of Schedule page, shows list of days 
 * @author Tom Hegarty
 * 
 * @returns <div className="Schedule">
 */
class Schedule extends React.Component {
    render () {

      return (
        <div className="Schedule">
          <h1>Schedule</h1>
          <Day day={"Monday"} dayInt={1}/>
          <Day day={"Tuesday"} dayInt={2}/>
          <Day day={"Wednesday"} dayInt={3}/>
          <Day day={"Thursday"} dayInt={4}/>
          <Footer />  
        </div>
      )
    }
}

export default Schedule; 