import React from 'react';

class UpdateItem extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      newtitle: ""
    }

  }


  handleDescriptionChange = (e) => {
    this.setState({newtitle:e.target.value})
  }

  handleDescriptionUpdate = () => {
    this.props.handleUpdateClick(this.props.details.sessionId, this.state.newtitle)
  }

  render() {
    let adminarea = "login as admin to update records";


    if(this.props.admin > 0){
      adminarea = <div className="adminArea">
        <p>Enter New Session Title ▼</p>
        <textarea
          rows="3" cols="50"
          placeholder = "Enter New Title Name Here"
          onChange={this.handleDescriptionChange}
        />
        <button className="UpdateSessionButton" onClick={this.handleDescriptionUpdate}>Update Title</button>
      </div>;
    }

    return (
      <div className="updateSession">
        <h2><span className="underline">{this.props.details.SessionName}</span></h2>
        <p><b>Session ID:</b> {this.props.details.sessionId}</p>
        <p><b>Session Chair:</b> {this.props.details.SessionChair}</p>
        {adminarea}
      </div>
    );
  }
}

export default UpdateItem;