import React from 'react';
import AuthorPresentations from './AuthorPresentations.js'

/**
 * component showing basic author details, futher click loads more details.
 * @author Tom Hegarty
 * 
 * @props details {obj} mapped data from fetch in Authors
 * @return <div className="author">
 */
class Author extends React.Component {

    state = { 
        display: false,
        data: []  
    }

    loadDetails = () => {
      const url = "http://unn-w16017590.newnumyspace.co.uk/kf6012/part1/api/authorContent?id=" + this.props.details.authorId

      fetch(url)
          .then( (response) => response.json() )
          .then( (data) => {
              this.setState({data:data.data})
          })
          .catch ((err) => {
              console.log("something went wrong ", err)
          }
      );
    }
  
    handleDetailsClick = () => {
      this.setState({display:!this.state.display})
      this.loadDetails()
    } 


    render() {
      let info = "";
      let buttonValue = "Show Papers ▲";
 
      let papers = ""
      if (this.state.display) {
        papers = this.state.data.map( (details, i) => (<AuthorPresentations key={i} details={details} />) )
      }
   
      if (this.state.display) {
      info = <div className="papersDisplayBox"><p>{this.state.data.title}</p>{papers}</div>
        buttonValue =  "Hide Papers ▲";
      }else{
        buttonValue =  "Show Papers ▼";
      }

      return (
        <div className="author">
          <h2> <span className="underline">{this.props.details.name}</span></h2>
          <p><b> Institution:</b> {this.props.details.authorInst}</p>
          <button id="detailsButton"onClick={this.handleDetailsClick}>{buttonValue}</button>
          {info}
        </div>
      );
    }
  }

  export default Author;